@use '../colors.scss';

.nav__burger,
.nav__cross {
	position: relative;
	display: none;
	margin: 1rem 2rem;
	padding: 0.6rem;
	width: 4rem;
	height: 6rem;
	background-color: colors.$navBg;
	border: none;
	cursor: pointer;
	transition: transform 0.3s;
}

.nav__burger:hover,
.nav__cross:hover {
	transform: scale(1.05);
}

.nav__burger {
	&--line {
		position: absolute;
		display: inline-block;
		top: 50%;
		left: 0;
		height: 0.2rem;
		width: 100%;
		background-color: colors.$mainBg;
	}
}

.nav__burger::before {
	content: '';
	position: absolute;
	top: 30%;
	left: 0;
	width: 100%;
	height: 0.2rem;
	background-color: colors.$mainBg;
}

.nav__burger::after {
	content: '';
	position: absolute;
	top: 70%;
	left: 0;
	width: 100%;
	height: 0.2rem;
	background-color: colors.$mainBg;
}

.nav__cross::before {
	content: '';
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	height: 0.2rem;
	background-color: colors.$mainBg;
	transform: rotate(45deg);
}

.nav__cross::after {
	content: '';
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	height: 0.2rem;
	background-color: colors.$mainBg;
	transform: rotate(-45deg);
}
