@use '../colors.scss';
@use '../mixins.scss';

.services {
	width: 100vw;

	&__content {
		width: 100%;
	}

	&__title {
		@include mixins.title;
	}

	&__container {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;
	}

	.subtitle-first {
		margin-bottom: 4rem;
	}

	.subtitle-next {
		margin: 4rem 0;
	}

	&__subtitle {
		text-align: center;
		font-size: 2.4rem;
		color: colors.$navBg;
		font-weight: 600;
	}

	&__box {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
	}

	&__img {
		margin: 0 1rem;
		width: 60rem;
		height: 40rem;
		object-fit: cover;
		border-radius: 0.6rem;
		box-shadow: 0 0 8px rgba(0, 0, 0, 0.342);
	}

	&__text {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin: 0 1rem;
		width: 70%;
		list-style: none;

		&--item {
			margin-bottom: 2.2rem;
			width: 70%;
			font-size: 1.6rem;
			color: black;
			font-weight: 500;
			text-align: left;
		}
	}
}
