.nav__burger,
.nav__cross {
  position: relative;
  display: none;
  margin: 1rem 2rem;
  padding: 0.6rem;
  width: 4rem;
  height: 6rem;
  background-color: #543c52;
  border: none;
  cursor: pointer;
  transition: transform 0.3s;
}

.nav__burger:hover,
.nav__cross:hover {
  transform: scale(1.05);
}

.nav__burger--line {
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 0;
  height: 0.2rem;
  width: 100%;
  background-color: #f1e8e6;
}

.nav__burger::before {
  content: "";
  position: absolute;
  top: 30%;
  left: 0;
  width: 100%;
  height: 0.2rem;
  background-color: #f1e8e6;
}

.nav__burger::after {
  content: "";
  position: absolute;
  top: 70%;
  left: 0;
  width: 100%;
  height: 0.2rem;
  background-color: #f1e8e6;
}

.nav__cross::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 0.2rem;
  background-color: #f1e8e6;
  transform: rotate(45deg);
}

.nav__cross::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 0.2rem;
  background-color: #f1e8e6;
  transform: rotate(-45deg);
}

.nav {
  position: fixed;
  height: 12rem;
  width: 100%;
  background-color: #543c52;
  z-index: 10;
}
.nav__content {
  position: relative;
  display: flex;
  justify-content: space-between;
}
.nav__logo {
  padding: 0 1.4rem;
  height: 12rem;
  border-radius: 50%;
}
.nav__links {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 12rem;
}
.nav__links--link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 14rem;
  height: 100%;
  color: #f1e8e6;
  font-size: 1.8rem;
  text-decoration: none;
  transition: background-color 0.3s, text-shadow 0.3s;
}
.nav__links--link:hover {
  background-color: #41293f;
  text-shadow: 0 0 2px #f1e8e6;
}

.header {
  position: relative;
  width: 100%;
  height: 65rem;
}
.header__hero-shadow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5960784314);
  z-index: 1;
}
.header__hero {
  background-image: url("../../dist/img/content/header-small.jpg");
  background-size: cover;
  background-position: 50% 50%;
  height: 100%;
}
.header__content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 1rem;
  height: 100%;
  z-index: 2;
}
.header__title, .header__text {
  margin-bottom: 3rem;
  color: #f1e8e6;
}
.header__title {
  font-size: 3.2rem;
  font-weight: 800;
}
.header__text {
  font-size: 2.8rem;
  font-weight: 500;
}
.header__btn {
  padding: 1.2rem 1.8rem;
  width: 20rem;
  font-size: 1.6rem;
  border-radius: 0.6rem;
  background-color: #325bfe;
  border: none;
  box-shadow: 0 0 3px #325bfe;
  font-weight: 500;
  text-decoration: none;
  text-align: center;
  color: #f1e8e6;
  text-transform: uppercase;
  cursor: pointer;
  transition: transform 0.3s;
}
.header__btn:hover {
  transform: scale(1.05);
}

.about__title {
  margin: 5rem 0;
  font-size: 3.2rem;
  text-align: center;
  color: #361d32;
  font-weight: bold;
  text-transform: uppercase;
}
.about__text-box {
  display: flex;
  flex-direction: column;
}
.about__item {
  margin: 1rem;
  font-size: 1.8rem;
  width: 55%;
  color: rgb(0, 0, 0);
}
.about .item--one {
  align-self: flex-start;
}
.about .item--two {
  margin-top: 1rem;
  align-self: flex-end;
}

.services {
  width: 100vw;
}
.services__content {
  width: 100%;
}
.services__title {
  margin: 5rem 0;
  font-size: 3.2rem;
  text-align: center;
  color: #361d32;
  font-weight: bold;
  text-transform: uppercase;
}
.services__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.services .subtitle-first {
  margin-bottom: 4rem;
}
.services .subtitle-next {
  margin: 4rem 0;
}
.services__subtitle {
  text-align: center;
  font-size: 2.4rem;
  color: #543c52;
  font-weight: 600;
}
.services__box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.services__img {
  margin: 0 1rem;
  width: 60rem;
  height: 40rem;
  object-fit: cover;
  border-radius: 0.6rem;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.342);
}
.services__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 1rem;
  width: 70%;
  list-style: none;
}
.services__text--item {
  margin-bottom: 2.2rem;
  width: 70%;
  font-size: 1.6rem;
  color: black;
  font-weight: 500;
  text-align: left;
}

.jobs {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}
.jobs__title {
  margin: 5rem 0;
  font-size: 3.2rem;
  text-align: center;
  color: #361d32;
  font-weight: bold;
  text-transform: uppercase;
}
.jobs__controllers--btn {
  margin: 0 1rem;
  padding: 0.8rem 1.6rem;
  border: none;
  background-color: #f55951;
  color: #f1e8e6;
  text-transform: uppercase;
  font-size: 1.8rem;
  font-weight: 500;
  border-radius: 0.6rem;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.377);
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}
.jobs__controllers--btn:hover {
  background-color: #e72a20;
  transform: scale(1.02);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-padding-top: 8rem;
  scroll-behavior: smooth;
  font-size: 62.5%;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background-color: #f1e8e6;
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
}

.wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;
}

.active-link {
  background-color: #361d32;
}

.mobile-nav-active {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: #543c52;
  z-index: 10;
}

.active-btn {
  background-color: #325bfe;
}

@media (width < 1150px) {
  .services__img {
    width: 50rem;
  }
}
@media (width < 860px) {
  .nav__links--link {
    width: 12rem;
  }
  .header__content {
    align-items: center;
  }
  .header__title, .header__text {
    text-align: center;
  }
  .about__item {
    width: 100%;
  }
  .about .item--one,
  .about .item--two {
    text-align: center;
    align-self: center;
  }
  .services__img {
    width: 45rem;
  }
}
@media (width > 750px) {
  .header__hero {
    background-image: url("../../dist/img/content/header-big.jpg");
  }
}
@media (width < 750px) {
  .nav {
    height: 8rem;
  }
  .nav__logo {
    height: 8rem;
  }
  .nav__links {
    position: absolute;
    display: none;
    flex-direction: column;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 40%;
  }
  .nav__links--link {
    width: 100vw;
  }
  .nav__burger {
    display: flex;
  }
  .services__box {
    flex-direction: column;
  }
  .services__box--second {
    flex-direction: column-reverse;
  }
  .services__img {
    width: 45rem;
  }
}
@media (width < 475px) {
  .services__img {
    height: 25rem;
    width: 35rem;
  }
}
@media (width < 450px) {
  .header__title {
    font-size: 2.8rem;
  }
  .header__text {
    font-size: 2.4rem;
  }
}
@media (width < 375px) {
  .services__img {
    height: 32rem;
    width: 28rem;
  }
}