@use '../colors.scss';
@use '../mixins.scss';

.jobs {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;

	&__title {
		@include mixins.title;
	}

    &__controllers{
        &--btn {
            margin: 0 1rem;
            padding: .8rem 1.6rem;
            border: none;
            background-color: colors.$btn;
            color: colors.$mainBg;
            text-transform: uppercase;
            font-size: 1.8rem;
            font-weight: 500;
            border-radius: .6rem;
            box-shadow: 0 0 8px rgba(0, 0, 0, 0.377);
            cursor: pointer;
            transition: background-color .3s, transform .3s;
        }

        &--btn:hover {
            background-color: colors.$btnHover;
            transform: scale(1.02);
        }
    }
}
