@use './colors.scss';
@use './mixins.scss';
@use './components/navigation';
@use 'components/header.scss';
@use 'components/about.scss';
@use 'components/services.scss';
@use 'components/jobs.scss';

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	scroll-padding-top: 8rem;
	scroll-behavior: smooth;
	font-size: 62.5%;
}

body {
	display: flex;
	flex-direction: column;
	align-items: center;
	min-height: 100vh;
	background-color: colors.$mainBg;
	font-family: 'Montserrat', sans-serif;
	overflow-x: hidden;
}

.wrapper {
	margin: 0 auto;
	width: 100%;
	max-width: 1400px;
}

.active-link {
	background-color: colors.$active;
}

.mobile-nav-active {
	position: absolute;
	display: flex;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	background-color: colors.$navBg;
	z-index: 10;
}

.active-btn {
    background-color: colors.$logo;
}

@media (width < 1150px) {
	.services {
		&__img {
			width: 50rem;
		}
	}
}

@media (width < 860px) {
	.nav {
		&__links {
			&--link {
				width: 12rem;
			}
		}
	}

	.header {
		&__content {
			align-items: center;
		}

		&__title,
		&__text {
			text-align: center;
		}
	}

	.about {
		&__item {
			width: 100%;
		}
		.item--one,
		.item--two {
			text-align: center;
			align-self: center;
		}
	}

	.services {
		&__img {
			width: 45rem;
		}
	}
}

@media (width > 750px) {
	.header {
		&__hero {
			background-image: url('../../dist/img/content/header-big.jpg');
		}
	}
}

@media (width < 750px) {
	.nav {
		height: 8rem;

		&__logo {
			height: 8rem;
		}

		&__links {
			position: absolute;
			display: none;
			flex-direction: column;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			height: 40%;

			&--link {
				width: 100vw;
			}
		}

		&__burger {
			display: flex;
		}
	}

	.services {
		&__box {
			flex-direction: column;

			&--second {
				flex-direction: column-reverse;
			}
		}

		&__img {
			width: 45rem;
		}
	}
}

@media(width < 475px) {
	.services {
		&__img {
			height: 25rem;
			width: 35rem;
		}
	}
}

@media (width < 450px) {
	.header {
		&__title {
			font-size: 2.8rem;
		}
		&__text {
			font-size: 2.4rem;
		}
	}
}

@media (width < 375px) {
	.services {
		&__img {
			height: 32rem;
			width: 28rem;
		}
	}
}
