@use '../colors.scss';
@use '../mixins.scss';
@use './navbtns.scss';

.nav {
	position: fixed;
	height: 12rem;
	width: 100%;
	background-color: colors.$navBg;
	z-index: 10;

	&__content {
		position: relative;
		display: flex;
		justify-content: space-between;
	}

	&__logo {
		padding: 0 1.4rem;
		height: 12rem;
		border-radius: 50%;
	}

	&__links {
		@include mixins.flex-center;
		height: 12rem;

		&--link {
			@include mixins.flex-center;
			width: 14rem;
			height: 100%;
			color: colors.$mainBg;
			font-size: 1.8rem;
			text-decoration: none;
			transition: background-color 0.3s, text-shadow 0.3s;
		}

		&--link:hover {
			background-color: colors.$navBgHover;
			text-shadow: 0 0 2px colors.$mainBg;
		}
	}
}