@use '../colors';
@use '../mixins';

.about {
	&__title {
		@include mixins.title;
	}

	&__text-box {
		display: flex;
		flex-direction: column;
	}

	&__item {
		margin: 1rem;
		font-size: 1.8rem;
		width: 55%;
		color: rgb(0, 0, 0);
	}
	
	.item--one {
		align-self: flex-start;
	}
	
	.item--two {
		margin-top: 1rem;
		align-self: flex-end;
	}
}