@use '../colors.scss';

.header {
	position: relative;
	width: 100%;
	height: 65rem;

	&__hero-shadow {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #00000098;
		z-index: 1;
	}

	&__hero {
		background-image: url('../../dist/img/content/header-small.jpg');
		background-size: cover;
		background-position: 50% 50%;
		height: 100%;
	}

	&__content {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 1rem;
		height: 100%;
		z-index: 2;
	}

	&__title,
	&__text {
		margin-bottom: 3rem;
		color: colors.$mainBg;
	}

	&__title {
		font-size: 3.2rem;
		font-weight: 800;
	}

	&__text {
		font-size: 2.8rem;
		font-weight: 500;
	}

	&__btn {
		padding: 1.2rem 1.8rem;
		width: 20rem;
		font-size: 1.6rem;
		border-radius: 0.6rem;
		background-color: colors.$logo;
		border: none;
		box-shadow: 0 0 3px colors.$logo;
		font-weight: 500;
		text-decoration: none;
        text-align: center;
		color: colors.$mainBg;
		text-transform: uppercase;
		cursor: pointer;
		transition: transform 0.3s;
	}

	&__btn:hover {
		transform: scale(1.05);
	}
}
