@use 'colors.scss';

@mixin flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin title {
    margin: 5rem 0;
    font-size: 3.2rem;
    text-align: center;
    color: colors.$active;
    font-weight: bold;
    text-transform: uppercase;
}

